* {
  margin: 0;

 padding: 0;

 border: 0;
}

html {
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #eaebed;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: -1;

}
#root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #eaebed;
  z-index: -3;

}
.app {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  font-size: 40rem;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
}



#homeLayout {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
  background-color: #eaebed;
}

.topBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-item-align: start;
      align-self: flex-start;
  background: url('Texture.svg'), -webkit-gradient(linear, left top, right top, from(#FF3CAC), to(#784BA0));
  background: url('Texture.svg'), -webkit-linear-gradient(left, #FF3CAC, #784BA0 100%);
  background: url('Texture.svg'), -o-linear-gradient(left, #FF3CAC, #784BA0 100%);
  background: url('Texture.svg'), linear-gradient(90deg, #FF3CAC, #784BA0 100%);
  background-blend-mode: multiply, normal;
  mix-blend-mode: multiply;
  width: 100%;
}

#bannerContainer {
  font-size: 2rem;
  width: 100%;
  text-align: center;
  height: 15rem;
  background-color: white;
  color: #5C5C5C;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  border-top: 2px solid #5C5C5C;
  overflow-x: hidden;

}

#transparent {
  position: absolute;
  height: 100%;
  top: calc(14rem + 105px);
  padding: 15px;
  font-size: 2rem;
  width: 100%;
  z-index: 20000;
  text-align: center;
  background-color: rgba(169, 169, 170, 0.4);
  color: #282828;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  border-top: 2px solid #5C5C5C;
  border-bottom: 2px solid #5C5C5C;
}
.joinTxt {
  margin-top: 15%;
  padding: 10px;
  padding-right: 25px;
}




#appNameContainer {
  font-size: 10rem;
  width: 100%;
  height: 100%;
  line-height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.appName {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 2.5rem;
  text-align: left;
  letter-spacing: 0.18em;
  font-variant: small-caps;
  color: white;
  margin-left: 2rem;
  margin-top: 1rem;
  margin-bottom: 3rem;
  z-index: 1;
}

.logoContainer {
  -ms-flex-order: 2;
      order: 2;
  margin-bottom: 1rem;
  margin-top: -.5rem;
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-left: 20px;
  overflow-x: auto;
}

.logo {
  width: 1.7rem;
  -webkit-transform: rotate(0deg);


}

.test {
  margin-top: 40%;
  font-size: 20px;
}



.navLink {
  text-decoration: none;
  width: 100%;
}



.helloBtn {
  width: 200px;
  height: 200px;
  display: inline-block;
  position: relative;
  margin-top: 30%;
  z-index: 20
}

@media (max-width: 800px) {
  .app {
    height: calc(100% + 1100px);
  }

}

@media (max-height: 700px) {

  #bannerContainer {
    height: 8rem;
  }
  #transparent {
    top: calc(3.5rem + 160px);
  }

}




@media (max-width: 420px) {
  .appName {
    margin-bottom: 3rem;
  }
  #bannerContainer {
    height: 9rem;
  }
  #transparent {
    top: calc(5.5rem + 160px);
  }
  .card {
    height: 10rem;
  }
}


@media (max-width: 350px) {
  .appName {
    margin-bottom: 1.5rem;
  }
  #bannerContainer {
    height: 8rem;
  }
  #transparent {
    top: calc(6.5rem + 160px);
  }

}



