.card {
  margin-top: calc(3rem + 30px);
  margin-bottom: calc(3rem + 10px);
  height: 25rem;
  width: 400px;
  top: 0;
  background-color: white;
  text-align: center;
  border: 2px solid #5C5C5C;
  overflow-x: hidden;

}

@media (max-width: 332px) {

  .card {
    height: 20rem;
  }
}

@media (max-width: 600px) {

.card {
  width: 80%;
}

}

@media (max-height: 880px) {

  .card {

  }
}
@media (max-height: 700px) {

  .card {
    margin-top: 0rem;
  }
}



